@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}.cf::after{content:"";clear:both;display:block}body{font-family:'Nanum Gothic', sans-serif}ul{list-style:none}a{text-decoration:none;color:#222}

*{margin:0;padding:0;box-sizing:border-box}body{display:flex;justify-content:center;height:60vh}body .bigbox{width:1000px;height:400px;background:#333;padding:50px;box-shadow:10px 10px 10px #333;margin-top:100px}body .bigbox .points{width:100%;height:80%;margin-top:-80px;display:flex;justify-content:space-between}body .bigbox .points .midbox,body .bigbox .points .smbox{box-shadow:10px 10px 10px #333;background:#555;font-weight:bold;display:flex;justify-content:center;align-items:center;position:relative}body .bigbox .points .midbox:before,body .bigbox .points .midbox:after,body .bigbox .points .smbox:before,body .bigbox .points .smbox:after{background:#ccc;content:'';width:10px;height:50px;position:absolute;top:-25px;border-radius:5px}body .bigbox .points .midbox:before,body .bigbox .points .smbox:before{left:20%}body .bigbox .points .midbox:after,body .bigbox .points .smbox:after{right:20%}body .bigbox .points .midbox{width:230px;height:300px;color:#fff;font-size:250px}body .bigbox .points .smbox{width:140px;height:200px;color:#f55;font-size:150px}body .bigbox .pcontainer{display:flex;justify-content:space-between;width:100%;margin-top:90px}body .bigbox .pcontainer p{width:400px;height:80px;line-height:80px;color:#fff;font-size:60px;font-weight:bold}body .bigbox .pcontainer p.right{text-align:right}body fieldset{margin-top:150px;padding:20px}body fieldset .buttons{display:flex;justify-content:space-between}body fieldset .buttons .leftbutton,body fieldset .buttons .rightbutton,body fieldset .buttons .centerbuttons{display:flex;justify-content:space-around}body fieldset .buttons .leftbutton,body fieldset .buttons .rightbutton{width:300px}body fieldset .buttons .centerbuttons{width:350px}body fieldset .buttons .centerbuttons .leftcenterbutton,body fieldset .buttons .centerbuttons .rightcenterbutton{display:flex;justify-content:space-between;width:35%}body fieldset .buttons button{width:50px;font-size:30px}body fieldset .inputcontainer{display:flex;justify-content:space-between;margin-top:30px;padding:0 20px}body fieldset .inputcontainer input{height:30px;line-height:30px;width:200px;font-size:30px;padding:0 10px}body fieldset .inputcontainer input:last-of-type{text-align:right}

